import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { QueryPlanBean } from 'app/pages/tzb/custom/marketing/marketing-project/query-plan/bean/QueryPlan.bean';
@Component({
	selector: 'select-marketing-prograss',
	templateUrl: './select-marketing-prograss.component.html',
	styleUrls: ['./select-marketing-prograss.component.scss'],
	providers: [CusOperationHttpService]
})
export class SelectMarketingPrograss implements OnInit {
	ch: any = CALENDAR_CN;
	@Input() inValue: any;
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	startDate: Date = null;
	thruDate: Date = null;
	msgs: Message[] = [];
	treeOrgName: any;
	treeOrgId: any;
	isShowTip: boolean = true;
	orgTreeDisplay: boolean = false;
	custMajorOrgId: string;
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	headerTitle: string;
	showModel: string;
	creater: string;//创建人
	createrName: string;//创建名字
	marketingProgramId: string//营销活动编号
	queryPlanBean: QueryPlanBean = new QueryPlanBean();
	first: number = 0;//分页控制
	totalNum: number;//总页数
	list: any = [];
	status: any = [];//营销方案状态
	//获取所有码值
	code: any;
	updateValue: any = [];
	//接受选中的值
	checkListPro: any = [];
	// marketingCampaignId: string = ""; //单选选中(探索ID)的值
	selectedCities: any = [];
	personPage: boolean = false;
	userInValue: string;
	statusId: string;
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.status = this.code['MarketingStatusPro'];
	}

	ngOnInit() {
		this.startDate = null;
		this.thruDate = null;
		this.marketingProgramId = this.routeInfo.snapshot.params['marketingProgramId'];
		this.statusId = '3';
		this.query();
	}
	//查询
	query() {
		this.queryPlanBean.countNum = 10;
		this.queryPlanBean.pageNum = 1;
		this.first = 0;
		this.doQuery();
	}
	doQuery() {
		this.queryPlanBean.marketingProgramId = this.marketingProgramId;
		this.queryPlanBean.startDate = this.commfunc.transDateN(this.startDate);
		this.queryPlanBean.thruDate = this.commfunc.transDateN(this.thruDate);
		this.queryPlanBean.statusId = this.statusId;
		// if (this.queryPlanBean.startDate || this.queryPlanBean.thruDate) {
		// 	if (!this.queryPlanBean.startDate || !this.queryPlanBean.thruDate) {
		// 		this.msgs = [];
		// 		this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入完整的时间' });
		// 		return;
		// 	}

		// }
		this.cusOperationHttpService.queryMarketingProgramList(this.queryPlanBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalNum = data.totalCount;
				this.list = data.returnList;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	//开始日期
	showDataStart() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}
	//页码重置
	querySecleReset() {
		this.queryPlanBean.countNum = 10;
		this.queryPlanBean.pageNum = 1;
		this.first = 0;
	}
	//重置
	reset() {
		this.queryPlanBean = new QueryPlanBean();
		this.querySecleReset();
		this.createrName = '';
		this.treeOrgName = '';
		this.startDate = null;
		this.thruDate = null;

	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//单选值
	checkClick2(item, event) {
		this.checkListPro = [item]
	}

	//选择按钮事件
	selectBtn() {
		this.checkListPro.forEach(element => {
			if (element.statusId == '3') {
				this.outValue.emit(this.checkListPro);
			} else {
				console.log(11111)
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择状态为已发布进行中！' });
				return;
			}
		});

	}
	//取消按钮
	quxiao() {
		this.outValue.emit(false)
	}
	paginate(event) {
		//每页显示的条数
		this.queryPlanBean.countNum = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.queryPlanBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.queryPlanBean.countNum;
		this.doQuery();
	}
	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.personPage = false;
	}
	//机构树的调用
	orgTreeShow() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;

	}
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.headerTitle = '机构';
		this.orgTreeDisplay = false;
		this.treeOrgName = param.orgName;
		this.queryPlanBean.orgId = param.orgId;
	}
	//人员模态框
	userTreeClick() {
		this.headerTitle = '人员';
		document.body.style.overflow = 'hidden';
		if (this.treeOrgName == '' || this.treeOrgName == undefined) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择所属机构！' });
			return
		}
		this.personPage = !this.personPage;
		this.userInValue = this.queryPlanBean.orgId;
		//选择了机构
		if (this.queryPlanBean.orgId != '' && this.queryPlanBean.orgId != null && this.queryPlanBean.orgId != undefined) {
			this.userInValue = this.queryPlanBean.orgId;
			return
		}
	}
	//人员回调
	perInfor(event) {
		this.personPage = false;
		this.createrName = event[0].tellerName;
		this.queryPlanBean.userId = event[0].tellerId;
		this.creater = event[0].tellerId;
	}
	//点击营销方案名称查看营销方案详情
	toDetail(item) {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', item])
	}

}
