	//详情
	copyBtn() {
		this.basicElementBean = new BasicElementBean();
		this.marketingHttpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let marketingParam = {
					marketingObjectType: data.marketingObjectType,
					exploreCondtionId: data.exploreCondtionId
				}
				this.commfunc.setSessionDataCH('marketingParam', JSON.stringify(marketingParam));
				if (this.flagCopy == '2') {//修改
					this.statusId = data.statusId;
					this.marketingObjectType = data.marketingObjectType;
					this.exploreCondtionId = data.exploreCondtionId;
					this.pushRules = data.pushRules;
					this.deId = data.deId;
					this.pushDate = data.pushDate;
					this.marketingTargetId = data.marketingTargetId
					if (data.marketingCampaignMap) {
						if (data.marketingCampaignMap.pushObject) {
							this.pushObject = data.marketingCampaignMap.pushObject;
						}
					}

					this.releaseQuantity = data.releaseQuantity;
					this.releasePersent = data.releasePersent;
					this.marketingObjectFileMap = data.marketingObjectFileMap;
					if (this.statusId == '3') {//状态为已发布进行中，只能修改营销活动说明
						this.showDisabled = true;
					} else {
						this.showDisabled = false;
					}
				}
				// this.commfunc.setSessionDataCH('marketingCampaigntType', JSON.stringify(data.marketingCampaigntType))
				if (data.marketingCampaigntType) {
					this.basicElementBean.marketingCampaigntType = data.marketingCampaigntType//任务类型
				}
				if (data.campaignName) {
					this.basicElementBean.campaignName = data.campaignName//营销名称
				}
				if (data.fromDate) {
					this.fromDate = new Date(data.fromDate);//营销活动开始时间
				}
				if (data.thruDate) {
					this.thruDate = new Date(data.thruDate);//营销活动结束时间
				}
				if (data.marMonitoringTimeFrom) {
					this.marMonitoringTimeFrom = new Date(data.marMonitoringTimeFrom);//营销监控开始时间
				}
				if (data.marMonitoringTimeTo) {
					this.marMonitoringTimeTo = new Date(data.marMonitoringTimeTo);//营销监控结束时间
				}
				if (data.marketingProgramId) {
					this.basicElementBean.marketingProgramId = data.marketingProgramId;
					this.marketingProgramId = this.basicElementBean.marketingProgramId;
				}
				if (data.marketingProgramName) {
					this.marketingProgramName = data.marketingProgramName;//营销方案的名称
				}
				this.basicElementBean.marketingContentDes = data.marketingContentDes;//营销活动说明

				if (data.marketingContentFileList) {
					//营销活动说明回显文件
					for (let i of data.marketingContentFileList) {
						this.fileList1.push(i);
					}
					this.basicElementBean.marketingContentFileList = this.fileList1;
				}
				this.basicElementBean.marketingTargetDes = data.marketingTargetDes;//营销活动目标	

				if (data.marketingTargetFileList) {
					for (let i of data.marketingTargetFileList) {
						this.fileList2.push(i);
					}//营销活动目标回显文件
					this.basicElementBean.marketingTargetFileList = this.fileList2;
				}
				if (data.remark) {
					this.basicElementBean.remark = data.remark;//客户来源
				}

				if (this.expCondNum && this.expCondNum == '14') {
					this.basicElementBean.marketingCampaigntType = data.marketingCampaigntType;
					this.marketChange();
				}
			}
		})
	}
	//下载
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);

	}
	//创建角色
	roleClick(marketingCampaignId) {
		let param = {
			marketingCampaignId: marketingCampaignId,
			marketingCampaigRole: '1',
			userId: this.userId,
			orgId: this.orgId,
		}
		this.marketingHttpService.createMarketingCampaignRole(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})

	}
	//删除
	toShanChu(i) {
		this.fileList1.splice(i, 1)
		this.fileList1 = _.clone(this.fileList1);

	}
	doDelete(i) {
		this.fileList2.splice(i, 1)
		this.fileList2 = _.clone(this.fileList2);
	}
	nameKey(param) {
		if (param == '1') {
			if (this.basicElementBean.campaignName) {
				if (this.basicElementBean.campaignName.length > 100) {
					this.showName = true;
				} else {
					this.showName = false;
				}
			}
		}
		if (param == '2') {
			if (this.basicElementBean.marketingContentDes) {
				if (this.basicElementBean.marketingContentDes.length > 255) {
					this.showName1 = true;
				} else {
					this.showName1 = false;
				}
			}
		}
		if (param == '3') {
			if (this.basicElementBean.marketingTargetDes) {
				if (this.basicElementBean.marketingTargetDes.length > 255) {
					this.showName2 = true;
				} else {
					this.showName2 = false;
				}
			}
		}
	}
